@charset "UTF-8";

/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/

// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";

// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------


// ---------------------------------------------------------------
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";

.page-company {
  .shosai {
    width: 398px;
    height: 20px;
    background: #f4f4ec;
    border: 1px solid #ddd;
    padding: 20px;
    font-weight: bold;
    color: #f60;
    margin: 20px 0 20px 0;
    a {
      color: #f60;
      text-decoration: underline;
    }
    &:before {
      content: ">";
    }
  }

  /*ページ内リンク*/

  .company_list {
    text-align: right;
    font-size: 0.8em;
    margin-bottom: 30px;
    li {
      display: inline;
      list-style: none;
      a {
        color: #333;
      }
    }
  }

  .topLink {
    a {
      @include font-line(13);
      color: #333;
    }
  }

  /*代表あいさつ*/

  &.page-message {
    .message {
      width: 100%;
      p {
        @include font-line(14);
        margin: 0 0 6px;
      }
      .txt {
      }
      .photo {
        margin-bottom: 15px;
        img {
          width: 700px;
          height: auto;
        }
      }
    }
  }

  .company_p {
    float: right;
    padding: 0 0 40px 30px;
    line-height: 160%;
    min-height: 311px;
  }

  .c_name {
    font-size: 1.4em;
  }

  /*会社概要*/

  #company_profile {
    margin: 0 0 20px 0;
    padding: 10px;
    font-size: 0.9em;
    dt {
      width: 120px;
      padding: 10px;
    }
    dd {
      margin: -40px 0 0 0;
      padding: 10px 10px 10px 140px;
      border-bottom: 1px dotted #CCCCCC;
    }
  }

  /*店舗紹介*/

  #company_photo {
    padding: 15px;
    background: #F4F0E1;
  }

  .company_photo_box {
    float: left;
    width: 320px;
    padding: 10px;
    font-size: 0.8em;
  }

  /*コンセプト*/

  #company_concept {
    width: 630px;
    height: 880px;
    padding: 50px 40px;
    background: url(img/company_concept.gif) no-repeat;
  }

  .company_pic {
    position: relative;
    width: 690px;
    height: 146px;
    margin: 0 auto 20px;
    padding: 0;
    img {
      float: left;
      margin-right: 15px;
      &:last-child {
        margin-right: 0;
      }
    }
    .pic_txt {
      clear: left;
      margin-bottom: 20px;
      padding-top: 5px;
      font-size: 0.8em;
      color: #666;
    }
  }

  /*企業理念*/

  .philosophy_box {
    margin: 0 0 30px 0;
    font-size: 0.9em;
    line-height: 20px;
    .subtitle {
      margin: 0 0 25px 0;
    }
    p {
      margin: 0 0 15px 0;
    }
    strong {
      font-size: 1.4em;
      margin: 0 0 1px 0;
    }
  }

  /* ---------------------------------------------------------------------
  company staff
  --------------------------------------------------------------------- */

  .staffbox {
    clear: both;
    margin-bottom: 10px;
    border: 7px #e5e5e5 solid;
    background: #e5e5e5;
    .innerWrap {
      padding: 10px;
      background: #fff;
      zoom: 1;
      &:after {
        content: ".";
        display: block;
        height: 0px;
        clear: both;
        line-height: 0;
        visibility: hidden;
      }
    }
    .txt {
      float: left;
      width: 400px;
      th {
        width: 40%;
        padding: 10px 0 10px 10px;
        font-weight: normal;
        text-align: left;
        border-bottom: 1px #e5e5e5 solid;
      }
    }
    .txt2 {
      float: left;
      width: 430px;
      th {
        width: 35%;
        padding: 10px 0 10px 10px;
        font-weight: normal;
        text-align: left;
        border-bottom: 1px #e5e5e5 solid;
      }
    }
    .txt td, .txt2 td {
      padding: 10px 0 10px 15px;
      text-align: left;
      border-bottom: 1px #e5e5e5 solid;
    }
    .photo {
      float: right;
    }
  }
}
